"use client";

import Link from "next/link";
import { useState } from "react";
import LoginPopup from "./login";
import { useAuth } from "@/context/authContext";

export default function Navbar() {
  const [showLogin, setShowLogin] = useState(false);
  const { loggedInUser, setLoggedInUser } = useAuth();

  const handleLoginSuccess = (username: string) => {
    console.log("Login sukses sebagai:", username);
    setLoggedInUser(username);
    setShowLogin(false);
  };

  const handleLogout = () => {
    const confirmLogout = window.confirm("Apakah Anda yakin ingin keluar?");
    if (!confirmLogout) return;

    localStorage.removeItem("token");
    setLoggedInUser(null);
  };

  return (
    <>
      <nav className="fixed top-0 w-full bg-gray-900 text-white py-3 shadow-md z-50">
        <div className="relative flex items-center px-4 h-14">
          <Link href="/" className="ml-6 text-2xl font-bold tracking-wide">
            WarPut
          </Link>

          <div className="absolute left-1/2 -translate-x-1/2 flex space-x-8 text-sm md:text-xl">
            <Link href="/" className="hover:underline">
              Beranda
            </Link>
            <Link href="#DaftarBarang" className="hover:underline">
              Daftar Barang
            </Link>
          </div>

          <div className="ml-auto mr-6 flex items-center space-x-4">
            {loggedInUser ? (
              <>
                <span className="text-lg">
                  Hello, <strong>{loggedInUser}</strong>
                </span>
                <button
                  onClick={handleLogout}
                  className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-sm"
                >
                  Keluar
                </button>
              </>
            ) : (
              <button
                onClick={() => setShowLogin(true)}
                className="bg-white text-black px-4 py-1 rounded-md text-lg hover:bg-gray-200"
              >
                Masuk
              </button>
            )}
          </div>
        </div>
      </nav>

      <LoginPopup
        isOpen={showLogin}
        onClose={() => setShowLogin(false)}
        onLoginSuccess={handleLoginSuccess}
      />
    </>
  );
}
